import { Text, Accordion } from "@contentful/f36-components";

import "./HomeAccordion.css";

const HomeAccordion = (props) => {
  console.log(props.data);

  let healthItems = props.data;

  console.log(healthItems);

  let style

  // {
  //   healthItems.map((item) => {
  //     if (item[Object.keys(item)[0]].queuesHealth === 'unhealthy' || item[Object.keys(item)[0]].servicesHealth === 'unhealthy' || item[Object.keys(item)[0]].diskHealth === 'unhealthy') { bordercolor = "Red"; return bordercolor; }
  //   }
  //   )
  // }

  // if (item[Object.keys(item)[0]].queuesHealth === 'unhealthy' || item[Object.keys(item)[0]].servicesHealth === 'unhealthy' || item[Object.keys(item)[0]].diskHealth === 'unhealthy') { bordercolor = "Red"; return bordercolor; }
  
  // console.log(bordercolor);

  return (
    <Accordion>
      {healthItems.map((item) => {

        console.log(item[Object.keys(item)[0]])

        if (
          item[Object.keys(item)[0]].queuesHealth === "unhealthy" ||
          item[Object.keys(item)[0]].servicesHealth === "unhealthy" ||
          item[Object.keys(item)[0]].diskHealth === "unhealthy"
        ) {
          style = {
            "borderLeft": "10px solid IndianRed"           
          };                
        }  else { style = {"borderLeft": "10px solid YellowGreen"}}

        return (
          <div style={style} key={Object.keys(item)[0]}>
            <Accordion.Item title={Object.keys(item)[0]}>
              <Text>
                <div className="heatlhItemGrid">
                  <div className="healthItemPara">Services : </div>
                  <div className="healthItemParaStatus">
                    {item[Object.keys(item)[0]].servicesHealth}
                  </div>
                </div>
                <div className="heatlhItemGrid">
                  <div className="healthItemPara">Mail Queues : </div>
                  <div className="healthItemParaStatus">
                    {item[Object.keys(item)[0]].queuesHealth}
                  </div>
                </div>
                <div className="heatlhItemGrid">
                  <div className="healthItemPara">Disk Space : </div>
                  <div className="healthItemParaStatus">
                    {item[Object.keys(item)[0]].diskHealth}
                  </div>
                </div>
              </Text>
            </Accordion.Item>
          </div>
        );}
      
 
  
       
      )}
    </Accordion>
  );
  
}

export default HomeAccordion
