/* eslint-disable array-callback-return */
/* eslint-disable react-hooks/exhaustive-deps */
import { useState, useCallback, useEffect } from "react";
import "./SpamStats.css";
import * as React from "react";
import enTranslations from "@shopify/polaris/locales/en.json";
import { AppProvider, Page, DatePicker } from "@shopify/polaris";
import SpamChart from "./SpamChart";
import axios from "axios";


// const disabledDates = [];

// disabledDates.push(new Date("2022-11-09T00:00:00"));

const SpamStats = () => {
    const [{ month, year }, setDate] = useState({ month: 10, year: 2022 });
    
  const [selectedDate, setSelectedDate] = useState({
    start: new Date("1999-1-1"),
    end: new Date("1999-1-1"),
  });
    const [datesWithSenderData, setDatesWithSenderData] = useState([])
    const [disabledDates, setDisabledDates] = useState([])
   
    const distinctDates = async function () {
      let distinctDatesJSON;
     
      
        distinctDatesJSON = await axios.get(
          `https://mailstats-api.onrender.com/api/v1/spamStatsDates`
        );

        if (distinctDatesJSON.status === 200) {
        //   console.log("Distinct Dates Retrieved");

        //   console.log(distinctDatesJSON.data);
        }
      

        setDatesWithSenderData(distinctDatesJSON.data)

        console.log(distinctDatesJSON.data[0])
        
    };

    
    useEffect(() => { distinctDates() }, [])


    const disabledDatesArray = [];

    
    
    
    const formatDatesWithData = function (month, year) {

        // console.log(month, year)

        const fullMonth = String(month+1).padStart(2, 0);

        const monthYearString = `${year}-${fullMonth}`

        const allDatesThisMonth = document.getElementsByClassName('Polaris-DatePicker__Day')

        const allDatesThisMonthArray = Array.from(allDatesThisMonth)

        // console.log(monthYearString)

        allDatesThisMonthArray.forEach((el) => {

            const fullDate = String(el.innerText).padStart(2, 0);
            
            const dateMonthYearString = `${monthYearString}-${fullDate}`;

            // console.log(dateMonthYearString)

            if ((datesWithSenderData).includes(dateMonthYearString)) {

                // console.log(el.classList)

                el.classList.toggle("Polaris-DatePicker__Day__withData");

                // console.log(dateMonthYearString);
                
                //  console.log(el.classList);
            }


            if (!datesWithSenderData.includes(dateMonthYearString)) {
              

            
                
                let dateMonthYearStringWithOffset = dateMonthYearString.concat("T00:00:00")

           

              disabledDatesArray.push(new Date(dateMonthYearStringWithOffset));
            }



        })

        // console.log(disabledDatesArray)
       
        setDisabledDates(disabledDatesArray);
    
    }
    
    useEffect(() => {
      formatDatesWithData(month, year);
    }, [month, year, datesWithSenderData]);
    

  const handleMonthChange = useCallback(
    (month, year) => setDate({ month, year }),
    []
    );
    
    // console.log(datesWithSenderData);

  return (
    <div className="SpamStats">
      <div className="pagelabelSpam">
        <p>Top Senders</p>
      </div>
      <div className="datePicker">
        <AppProvider i18n={enTranslations}>
          <Page>
            <DatePicker
              month={month}
              year={year}
              onChange={setSelectedDate}
              onMonthChange={handleMonthChange}
              selected={selectedDate}
              disableDatesBefore={new Date("2022-10-31")}
              disableDatesAfter={new Date()}
              disableSpecificDates={disabledDates}
            />
          </Page>
        </AppProvider>
      </div>

      <div className="WrapperSpam">
        <SpamChart date={selectedDate} />
      </div>
    </div>
  );
};

export default SpamStats;
