/* eslint-disable react-hooks/exhaustive-deps */
import "./Home.css";
import axios from "axios";
import { useState, useEffect } from "react";
import { GridLoader } from "react-spinners";
import HomeAccordion from "./HomeAccordion";

function Home() {
  const [serverHealth, setServerHealth] = useState([]);
  const [loading, setLoading] = useState(false);

  let initialHealthObject

  let serversArray

  const readMailStatsDB = async function () {
    setLoading(true);

    const certDataJSONDisk = await axios.get(
      `https://mailstats-api.onrender.com/api/v1/diskStats`
    );

    const DISK_STATS_SORTED_ASC = certDataJSONDisk.data.sort((objA, objB) => {
      return objA.serverName.localeCompare(objB.serverName);
    });

    const DBObjectServers = DISK_STATS_SORTED_ASC.filter(
      (el) => el.serverName !== "NoData"
    );

    console.log(DBObjectServers);

    serversArray = new Array(0);

    DBObjectServers.forEach((el) => {
      serversArray.push(el.serverName);
    });

    initialHealthObject = new Array(0);

    serversArray.forEach((el) => {
      let newObj = {};

      newObj[el] = {
        diskHealth: "healthy",
        servicesHealth: "healthy",
        queuesHealth: "healthy",
       
      };

      initialHealthObject.push(newObj);
    });

    DBObjectServers.forEach((el) => {
      el.volumes.forEach((vol) => {
        if (vol.freeSpaceGB / vol.capacityGB < 0.15) {
          console.log(`${el.serverName} unheathy volumes`);

          let objectToUpdate = initialHealthObject.find((item) =>
            Object.keys(item).includes(el.serverName)
          );

          objectToUpdate[el.serverName]["diskHealth"] = "unhealthy";
        }
      });
    });



 const certDataJSONServices = await axios.get(
   `https://mailstats-api.onrender.com/api/v1/serviceStats`
 );

 const SERVICE_STATS_SORTED_ASC = certDataJSONServices.data.sort((objA, objB) => {
   return objA.serverName.localeCompare(objB.serverName);
 });
    
   const DBObjectServices = SERVICE_STATS_SORTED_ASC.filter(
     (el) => el.serverName !== "NoData"
   ); 
    
    console.log(DBObjectServices); 

    let servicesServerArray = []
    
DBObjectServices.forEach((el) => servicesServerArray.push(el.serverName));

    console.log(servicesServerArray);
    
    serversArray.forEach((el) => {
      if ((servicesServerArray).includes(el)) {
      
       let objectToUpdate = initialHealthObject.find((item) =>
         Object.keys(item).includes(el)
       );

       objectToUpdate[el]["servicesHealth"] = "unhealthy"; 


     } })



 const certDataJSONQueues = await axios.get(
   `https://mailstats-api.onrender.com/api/v1/queueStats`
 );

 const QUEUE_STATS_SORTED_ASC = certDataJSONQueues.data.sort(
   (objA, objB) => {
     return objA.serverName.localeCompare(objB.serverName);
   }
 );

 const DBObjectQueues = QUEUE_STATS_SORTED_ASC.filter(
   (el) => el.serverName !== "NoData"
 );

    console.log(DBObjectQueues); 
    
   let queuesServerArray = [];

   DBObjectQueues.forEach((el) => queuesServerArray.push(el.serverName));

   console.log(queuesServerArray);

   serversArray.forEach((el) => {
     if (queuesServerArray.includes(el)) {
       let objectToUpdate = initialHealthObject.find((item) =>
         Object.keys(item).includes(el)
       );

       objectToUpdate[el]["queuesHealth"] = "unhealthy";
     }
   });  


    if (certDataJSONQueues.status === 200) {
      console.log(initialHealthObject);
      setServerHealth(initialHealthObject);
      setLoading(false);
    }
  };

  

  


  useEffect(() => {
    readMailStatsDB();
     
  }, []);

  useEffect(() => {
    const timer = setInterval(() => {
      readMailStatsDB();
      
    }, 30000);

    return () => clearInterval(timer);
  }, []);

  const loadSpinner = (
    <GridLoader color="#4f5e7d" loading={loading} size={33} />
  );

  console.log(serverHealth);

  return (
    <div className="home">
      <p>Mail Stats</p>
      <div className="spinnerSVC">{loadSpinner}</div>

      <div className="WrapperAccordion">
        <HomeAccordion data={serverHealth} />
      </div>

      
    </div>
  )
}

export default Home
