/* eslint-disable react-hooks/exhaustive-deps */
import axios from "axios";
import "./SpamStats.css";
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
} from "recharts";
import { useState, useEffect } from "react";
import { GridLoader } from "react-spinners";

const outputExcludeArray = [
  "MicrosoftExchange329e71ec88ae4615bbc36ab6ce41109e@mahidol.ac.th",
];

let chartArrayTopSenders;

const SpamChart = (props) => {
  const [topSenders, setTopSenders] = useState([]);
  const [displayChart, setDisplayChart] = useState(false);
  const [loading, setLoading] = useState(false);
    const [xAxisMax, setXAxisMax] = useState(25000)
    const domain = [0,25000]

  const year = props.date.start.getFullYear();
  let month = props.date.start.getMonth() + 1;
  let date = props.date.start.getDate();

  month = String(month).padStart(2, 0);

  date = String(date).padStart(2, 0);

  const selectedDate = `${year}-${month}-${date}`;

  //   console.log(selectedDate);

  const readSpamStatsDB = async function (selectedDate) {
    let spamDataJSON;
    setDisplayChart(false);
    setLoading(true);

    // console.log(selectedDate);

    if (selectedDate !== "1999-0-1") {
      spamDataJSON = await axios.get(
        `https://mailstats-api.onrender.com/api/v1/spamStats/?date=${selectedDate}`
      );

      if (spamDataJSON.status === 200) {
        // console.log("Query Complete");
        setLoading(false);
        // console.log(spamDataJSON.data);
      }
    }

    return spamDataJSON.data;
  };

    async function outputToChart(selectedDate) {
        // console.log('START')
        const chartArray = await readSpamStatsDB(selectedDate);

        // console.log(chartArray);

        const CHART_ARRAY_SORTED_ASC = chartArray.sort(
            (objA, objB) => Number(objB.messageCount) - Number(objA.messageCount)
        );

        let chartArrayTop25 = CHART_ARRAY_SORTED_ASC.filter(
            (_, index) => index < 25
        );
        chartArrayTopSenders = chartArrayTop25.filter((el) => {
            return !outputExcludeArray.includes(el.sender);
        });
      
        if (chartArrayTopSenders.length > 0) { 
      
        console.log(chartArrayTopSenders[0].messageCount)

        setXAxisMax(chartArrayTopSenders[0].messageCount);

    }

    
    setTopSenders(chartArrayTopSenders);
    if (chartArrayTopSenders.length) {
      setDisplayChart(true);
    }
  }

  useEffect(() => {
    outputToChart(selectedDate);
  }, [selectedDate]);

  const loadSpinner = (
    <GridLoader color="#4f5e7d" loading={loading} size={33} />
    );
    
    
    domain[1] = Number(xAxisMax);

    

  if (loading) {
    return <div className="spinnerSVC">{loadSpinner}</div>;
  } else if (displayChart) {
    return (
      <div>
        <BarChart
          layout="vertical"
          width={1000}
          height={800}
          data={topSenders}
          margin={{
            top: 60,
            right: 180,
            left: 180,
            bottom: 5,
          }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis type="number" dataKey="messageCount" domain={domain} />
          <YAxis type="category" dataKey="sender" />
          <Tooltip />
          <Legend />
          <Bar dataKey="messageCount" fill="#3397a5" />
        </BarChart>
      </div>
    );
  }
};

export default SpamChart;
